html {
  background-color: rgb(44, 44, 44);
  min-height: 100vh;
  max-width: 100%;

  margin: 0 auto;
}
body {
  min-height: 100vh;
  max-width: 100%;

  margin: 0 auto;
}
#topbar {
  width: 100%;

  border-style: solid;
  box-shadow: 0 0.6em 0.5em -0.4em var(---boxcolor);
  ---boxcolor: royalblue;
  background-color: rgb(24, 24, 24);
  border-width: 0px 0px 3px 0px;
  border-color: royalblue;
}
button {
  color: var(--color);
  background: none;
  border: 2px solid;
  height: 50px;
  border-radius: 12.5px;
  font: inherit;
  line-height: 1;
  margin: 0.5em;
  padding: 0em 2em;
}
#buttons {
  --color: #19a3bc;

  top: 15px;
  left: 100px;
}

button:hover {
  box-shadow: 0em 0.2em 0.5em var(---boxcolor);
  ---boxcolor: royalblue;
}

#head {
  color: white;
  width: 500px;
  height: 250px;
  font-size: 50px;
  text-shadow: 2.5px 2.5px black, 0 0 3px #00d9ff;

  font-family: "Times New Roman", Times, serif;
  position: absolute;
  top: 250px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
}
